import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";

export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			loadingType: false,
			navList: [{
					state: 9,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '已支付',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '已收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '评价',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 5,
					text: '取消',
					loadingType: 'more',
					orderList: []
				}
			],
			orderList: [],
			uid:null
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		let id = uni.getStorageSync('user_id')
		this.uid = id;
		this.tabCurrentIndex = this.tabCurrentIndex //+options.state;




		if (options.state == 0) {
			this.loadData()
		}


	},

	methods: {
		//获取订单列表
		async loadData() {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;

			let navItem = this.navList[index];
			let state = navItem.state;

			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			navItem.loadingType = 'loading';

			uni.request({
				method: 'GET',
				url: `http://47.104.29.236:9999/api/v1/order_st/${state}?id=${this.uid}`, //仅为示例，并非真实接口地址。
				data: {},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						let orderList = res.data.data
						this.orderList = orderList
						this.$set(navItem, 'loaded', true);
						if (orderList.length == 0) {
							navItem.loadingType = 'more'
						} else {
							navItem.loadingType = 'noMore'
						}

					} else {}
				}
			});
		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData();
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			this.loadData();
		},
		//删除订单
		deleteOrder(index, id) {
			uni.showLoading({
				title: '请稍后'
			})
			uni.request({
				method: 'DELETE',
				url: `http://47.104.29.236:9999/api/v1/order_st/${id}`, //仅为示例，并非真实接口地址。
				data: {},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						this.orderList.splice(index, 1);
						uni.hideLoading();
					} else {}
				}
			});

		},
		//取消订单
		cancelOrder(index, id) {
			uni.showLoading({
				title: '请稍后'
			})

			uni.request({
				method: 'PUT',
				url: `http://47.104.29.236:9999/api/v1/order_st/${id}`, //仅为示例，并非真实接口地址。
				data: {
					type: 5
				},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						this.orderList.splice(index, 1);
						uni.hideLoading();
					} else {}
				}
			});
		},
		// 退款
		refund(index, id) {
			console.log(id)
			uni.showLoading({
				title: '请稍后'
			})

			uni.request({
				method: 'PUT',
				url: `http://47.104.29.236:9999/api/v1/order_st/refund/${id}`, //仅为示例，并非真实接口地址。
				data: {
					type: 1
				},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						this.loadData();
						uni.hideLoading();
					} else {}
				}
			});
		},
		// 取消退款
		unrefund(index, id) {
			console.log(id)
			uni.showLoading({
				title: '请稍后'
			})
		
			uni.request({
				method: 'PUT',
				url: `http://47.104.29.236:9999/api/v1/order_st/unrefund/${id}`, //仅为示例，并非真实接口地址。
				data: {
					type: 0
				},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						this.loadData();
						uni.hideLoading();
					} else {}
				}
			});
		},
		// 确认收货
		Receivingtake(index, id) {
			console.log(id)
			uni.showLoading({
				title: '请稍后'
			})

			uni.request({
				method: 'PUT',
				url: `http://47.104.29.236:9999/api/v1/order_st/${id}`, //仅为示例，并非真实接口地址。
				data: {
					type: 3
				},
				header: {
					// 'custom-header': 'hello' //自定义请求头信息
				},
				success: (res) => {
					if (res.data.ok === 1) {
						this.orderList.splice(index, 1);
						uni.hideLoading();
					} else {}
				}
			});
		},
		// 去支付
		payment(index,id) {
			let CurrentPages = getCurrentPages()
			let pay = {
				id:id,
				path:CurrentPages[CurrentPages.length-1].route
			}
			pay = JSON.stringify(pay)
			uni.setStorageSync('pay', pay);
			uni.redirectTo({
				url: `/pages/meituan/payment?id=${id}&path=${CurrentPages[CurrentPages.length-1].route}`
			});
		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
